import { Model } from '../base/Model';
export class AppData extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            payment: '',
            email: '',
            phone: '',
            address: '',
        };
        this.formErrors = {};
    }
    //   
    addBasket(item) {
        if (this.basket.indexOf(item) < 0) {
            this.basket.push(item);
            this.updateBasket();
        }
    }
    //,     .
    productOrder(item) {
        return Boolean(this.basket.find((basketItem) => basketItem.id === item.id));
    }
    //   
    clearBasket() {
        this.basket = [];
        this.updateBasket();
    }
    // 
    clearOrder() {
        this.order = {
            payment: '',
            address: '',
            email: '',
            phone: '',
        };
    }
    // 
    updateBasket() {
        this.emitChanges('counter:changed', this.basket);
        this.emitChanges('basket:changed', this.basket); //  
        this.emitChanges('catalog:changed', this.items); //  
    }
    //   
    removeFromBasket(id) {
        this.basket = this.basket.filter((it) => it.id !== id);
        this.emitChanges('basket:changed');
    }
    //   
    getOrderProducts() {
        return this.basket;
    }
    //  
    getTotalPrice() {
        return this.basket.reduce((total, item) => {
            return total + item.price;
        }, 0);
    }
    //     
    setCatalog(item) {
        this.items = item;
        this.emitChanges('items:changed', { catalog: this.items });
    }
    //  validateOrder
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    setRating(item) {
        this.players = item;
        this.emitChanges('rating:changed', this.players);
    }
}
